export default {
	data() {
		return {
			searchKey: "", //搜索关键词
			width: 200, //header宽度
			height: 64, //header高度
			inputTop: 0, //搜索框距离顶部距离
			arrowTop: 0, //箭头距离顶部距离
			dropScreenH: 0, //下拉筛选框距顶部距离
			tabIndex: 0, //顶部筛选索引
			isList: false, //是否以列表展示  | 列表或大图
			selectedName: "综合",
			selectH: 0,
			dropdownList: [{
				name: "综合",
				selected: true
			}, {
				name: "价格升序",
				selected: false,
			}, {
				name: "价格降序",
				selected: false,
			}],
			pageIndex: 1,
			pageLimit: 6,
			loadding: false,
			pullUpOn: true,
			productList: []
		}
	},
	onLoad: function(options) {
		let obj = {};
		obj = wx.getMenuButtonBoundingClientRect();
		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.inputTop = obj.top ? (obj.top + (obj.height - 30) / 2) : (res.statusBarHeight + 7);
				this.arrowTop = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				let searchKey = options.searchKey || "";
				if (searchKey) {
					searchKey = searchKey.split("-")
					this.searchKey = searchKey;
				} else {
					this.searchKey = "";
				}
				let type = options.type || 'category'
				this.getGoodsInfoByType(type);
				//略小，避免误差带来的影响
				this.dropScreenH = this.height * 750 / res.windowWidth + 89;
			}
		})
	},
	methods: {
		px(num) {
			return uni.upx2px(num) + "px"
		},
		showDropdownList: function() {
			this.selectH = 246;
			this.tabIndex = 0;
		},
		hideDropdownList: function() {
			this.selectH = 0
		},
		dropdownItem: function(e) {
			let index = e.currentTarget.dataset.index;
			let arr = this.dropdownList;
			for (let i = 0; i < arr.length; i++) {
				if (i === index) {
					arr[i].selected = true;
				} else {
					arr[i].selected = false;
				}
			}
			this.dropdownList = arr;
			this.selectedName = index == 0 ? '综合' : '价格';
			this.selectH = 0
		},
		screen: function(e) {
			let index = e.currentTarget.dataset.index;
			if (index == 0) {
				this.showDropdownList();
			} else if (index == 1) {
				this.tabIndex = 1
			} else if (index == 2) {
				this.isList = !this.isList
			}
		},
		search: function() {
			uni.navigateTo({
				url: '../news-search/news-search'
			})
		},
		detail: function(goodsId) {
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsId=' + goodsId
			})
		},
		back: function() {
			uni.navigateBack()
		},
		getGoodsInfoByType(type) {
			this.$api.getGoodsInfoByType({
				searchKey: this.searchKey,
				type: type
			}, (res) => {
				if (res.code) {
					this.productList = res.data
				}
			})
		}
	},
	onPullDownRefresh: function() {
		let loadData = JSON.parse(JSON.stringify(this.productList));
		loadData = loadData.splice(0, 10);
		this.productList = loadData;
		this.pageIndex = 1;
		this.pullUpOn = true;
		this.loadding = false;
		uni.stopPullDownRefresh()
	},
	onReachBottom: function() {
		// if (!this.pullUpOn) return;
		// this.loadding = true;
		// this.$api.getGoodsInfo({
		// 	page: this.pageIndex,
		// 	limit: this.pageLimit,
		// 	goodsName: this.searchKey
		// }, (res) => {
		// 	if (res.code) {
		// 		this.productList = this.productList.concat(this.$util.GetimgUrl(res.data, 'goodsImg'));
		// 		this.pageIndex = this.pageIndex + 1;
		// 		this.loadding = false
		// 	} else {
		// 		this.loadding = false;
		// 		this.pullUpOn = false
		// 	}
		// })
	}
}
